html {
  scroll-behavior: smooth;
}
body {
  margin: 0;

  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-size: 20px;
  line-height: 1.4;
  letter-spacing: 0.08em;

  color: var(--primaryTextColor);
  background-color: #ffffff;
  scroll-behavior: smooth;
}

.section {
  padding-top: 35px;
  padding-bottom: 35px;

  @include mq(desk) {
    padding-top: 65px;
    padding-bottom: 65px;
  }
}

.section-title {
  margin-bottom: 30px;

  font-weight: 800;
  font-size: 20px;
  line-height: 1.3;
  letter-spacing: 0.03em;
  text-align: center;

  color: var(--primaryTextColor);

  @include mq(tab) {
    font-size: 26px;
  }

  @include mq(desk) {
    font-size: 46px;
  }
}

.text-accent {
  color: var(--accent-color);
}
